<template>
  <div class="orders-page">
    <div class="orders-header">
      <h1>My Orders</h1>
    </div>

    <div class="orders-list" v-if="orders.length">
      <div v-for="order in orders" 
           :key="order.id" 
           class="order-card">
        <div class="order-header">
          <div class="order-info">
            <h3>Order #{{ order.id.slice(0,8) }}</h3>
            <p class="order-date">{{ formatDate(order.created_at) }}</p>
          </div>
          <span :class="['status-badge', order.status]">
            {{ order.status }}
          </span>
        </div>

        <div class="order-items">
          <div v-for="item in order.items" 
               :key="item.id" 
               class="order-item">
            <img :src="item.image_url" :alt="item.name">
            <div class="item-details">
              <h4>{{ item.name }}</h4>
              <p>Qty: {{ item.quantity }} Ã— ${{ item.price }}</p>
            </div>
          </div>
        </div>

        <div class="order-footer">
          <div class="order-total">
            <span>Total:</span>
            <strong>${{ order.total_amount.toFixed(2) }}</strong>
          </div>
          <div class="order-actions" v-if="order.status === 'approved'">
            <a v-if="order.stripe_session_id" 
               :href="getPaymentLink(order.stripe_session_id)"
               class="pay-button"
               target="_blank"
            >
              Pay Now
            </a>
          </div>
          <button class="view-details-btn" @click="viewOrderDetails(order)">
            <span class="material-symbols-rounded">visibility</span>
            View Details
          </button>
        </div>
      </div>
    </div>

    <div v-else class="empty-orders">
      <span class="material-symbols-rounded">receipt_long</span>
      <h2>No Orders Yet</h2>
      <p>When you place orders, they will appear here</p>
      <router-link to="/store" class="shop-button">
        Browse Store
      </router-link>
    </div>

    <!-- Add Order Details Modal -->
    <OrderDetailsModal
      v-if="selectedOrder"
      :order="selectedOrder"
      @close="selectedOrder = null"
    />
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import OrderDetailsModal from '@/components/OrderDetailsModal.vue'
import Modal from '@/components/Modal.vue'

export default {
  name: 'Orders',
  components: {
    OrderDetailsModal,
    Modal
  },
  setup() {
    const orders = ref([])
    const selectedOrder = ref(null)

    const fetchOrders = async () => {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) return

      const { data, error } = await supabase
        .from('orders')
        .select('*')
        .eq('user_id', user.id)
        .order('created_at', { ascending: false })

      if (error) {
        console.error('Error fetching orders:', error)
        return
      }

      orders.value = data
    }

    const formatDate = (date) => {
      return new Date(date).toLocaleString()
    }

    const getPaymentLink = (sessionId) => {
      return `/payment/${sessionId}`
    }

    const viewOrderDetails = (order) => {
      selectedOrder.value = order
    }

    onMounted(fetchOrders)

    return {
      orders,
      formatDate,
      getPaymentLink,
      selectedOrder,
      viewOrderDetails
    }
  }
}
</script>

<style scoped>
.orders-page {
  max-width: 1000px;
  margin: 0 auto;
  padding: 40px 24px;
}

.orders-header h1 {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.025em;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 40px;
}

.order-card {
  background: var(--card-background);
  border-radius: 20px;
  border: 1px solid var(--card-border);
  margin-bottom: 24px;
  overflow: hidden;
  transition: all 0.3s ease;
}

.order-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-primary);
}

.order-header {
  padding: 24px;
  border-bottom: 1px solid var(--card-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-info h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.order-date {
  margin: 4px 0 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.status-badge {
  padding: 6px 12px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

/* Status badges with gradients */
.status-badge.pending {
  background: linear-gradient(135deg, #ffd700, #ffa000);
  color: black;
}

.status-badge.approved {
  background: linear-gradient(135deg, #4CAF50, #45a049);
  color: white;
}

.status-badge.paid {
  background: linear-gradient(135deg, #2196F3, #1976D2);
  color: white;
}

.status-badge.shipped {
  background: linear-gradient(135deg, #9C27B0, #7B1FA2);
  color: white;
}

.status-badge.delivered {
  background: linear-gradient(135deg, #00BCD4, #0097A7);
  color: white;
}

.status-badge.cancelled {
  background: linear-gradient(135deg, #F44336, #D32F2F);
  color: white;
}

.order-items {
  padding: 20px;
}

.order-item {
  display: flex;
  gap: 16px;
  padding: 12px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.order-item:last-child {
  border-bottom: none;
}

.order-item img {
  width: 60px;
  height: 84px;
  object-fit: contain;
  border-radius: 8px;
  background: var(--background-elevated);
}

