}

.item-details p {
  margin: 4px 0 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.order-footer {
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-total {
  font-size: 18px;
}

.order-total strong {
  margin-left: 8px;
  color: var(--accent-primary);
}

.pay-button {
  padding: 8px 16px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s ease;
}

.pay-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(var(--accent-primary-rgb), 0.3);
}

.view-details-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: var(--surface-background);
  color: var(--text-primary);
  border: none;
  border-radius: 8px;
