  transform: translateY(-1px);
}

.empty-orders {
  text-align: center;
  padding: 64px 24px;
  color: var(--text-secondary);
}

.empty-orders .material-symbols-rounded {
  font-size: 48px;
  margin-bottom: 16px;
}

.empty-orders h2 {
  margin: 0;
  font-size: 24px;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.empty-orders p {
  margin: 8px 0 24px;
}

.shop-button {
  display: inline-block;
  padding: 12px 24px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.shop-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(var(--accent-primary-rgb), 0.3);
}

/* Add smooth transitions */
.order-card {
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>
