<template>
  <Modal @close="$emit('close')" :maxWidth="800">
    <template #header>
      <div class="modal-title">
        <h2>Order Details</h2>
        <div class="order-meta">
          <span>Order #{{ order.id.slice(0,8) }}</span>
          <span class="dot">â€¢</span>
          <span>{{ formatDate(order.created_at) }}</span>
        </div>
      </div>
    </template>

    <div class="order-details">
      <div class="details-header">
        <div class="order-meta">
          <h2>Order #{{ order.id.slice(0,8) }}</h2>
          <p class="order-date">{{ formatDate(order.created_at) }}</p>
        </div>
        <div :class="['status-badge', order.status]">
          {{ formatStatus(order.status) }}
        </div>
      </div>

      <div class="details-content">
        <!-- Items Section -->
        <div class="section items-section">
          <h3>Order Items</h3>
          <div class="items-list">
            <div v-for="item in order.items" :key="item.id" class="item">
              <img :src="item.image_url" :alt="item.name" @error="handleImageError">
              <div class="item-info">
                <h4>{{ item.name }}</h4>
                <div class="item-meta">
                  <span>Quantity: {{ item.quantity }}</span>
                  <span>${{ item.price }}</span>
                </div>
              </div>
              <div class="item-total">
                ${{ (item.price * item.quantity).toFixed(2) }}
              </div>
            </div>
          </div>
        </div>

        <!-- Shipping Section -->
        <div class="section shipping-section">
          <h3>Shipping Details</h3>
          <div class="address-info">
            <p><strong>{{ order.shipping_address.first_name }} {{ order.shipping_address.last_name }}</strong></p>
            <p>{{ order.shipping_address.address }}</p>
            <p>{{ order.shipping_address.postal_code }} {{ order.shipping_address.city }}</p>
            <p>{{ order.shipping_address.email }}</p>
          </div>

          <!-- Tracking Info if available -->
          <div v-if="order.tracking_info" class="tracking-info">
            <h4>Tracking Information</h4>
            <p><strong>Carrier:</strong> {{ order.tracking_info.carrier }}</p>
            <p><strong>Tracking Number:</strong> {{ order.tracking_info.number }}</p>
            <a :href="order.tracking_info.url" target="_blank" class="tracking-link">
              Track Package
              <span class="material-symbols-rounded">open_in_new</span>
            </a>
          </div>
        </div>

        <!-- Order Summary -->
        <div class="section summary-section">
          <h3>Order Summary</h3>
          <div class="summary-rows">
            <div class="summary-row">
              <span>Subtotal</span>
              <span>${{ calculateSubtotal().toFixed(2) }}</span>
            </div>
            <div class="summary-row">
              <span>Shipping</span>
              <span>Free</span>
            </div>
            <div class="summary-total">
              <span>Total</span>
              <span>${{ order.total_amount.toFixed(2) }}</span>
            </div>
          </div>
        </div>
      </div>

      <!-- Order Actions -->
      <div class="details-footer" v-if="order.status === 'approved'">
        <a v-if="order.stripe_session_id" 
           :href="getPaymentLink(order.stripe_session_id)"
           class="pay-button"
           target="_blank"
        >
          Complete Payment
        </a>
      </div>
    </div>
  </Modal>
</template>

<script>
import { defineComponent } from 'vue'
import Modal from './Modal.vue'

export default defineComponent({
  name: 'OrderDetailsModal',
  components: { Modal },
  props: {
    order: {
      type: Object,
      required: true
    }
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleString()
    },
    formatStatus(status) {
      return status.split('_').map(word => 
        word.charAt(0).toUpperCase() + word.slice(1)
      ).join(' ')
    },
    calculateSubtotal() {
      return this.order.items.reduce((sum, item) => 
        sum + (item.price * item.quantity), 0)
    },
    getPaymentLink(sessionId) {
      return `/payment/${sessionId}`
    },
    handleImageError(event) {
      event.target.src = '/placeholder.png'
    }
  }
})
</script>

<style scoped>
.modal-title {
  text-align: center;
}

.modal-title h2 {
  font-size: 24px;
  margin: 0;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.order-meta {
  margin-top: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  color: var(--text-secondary);
  font-size: 14px;
}

.dot {
  color: var(--text-tertiary);
}

.status-badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 100px;
  font-size: 13px;
  font-weight: 500;
  background: var(--surface-background);
  margin-top: 16px;
}

.section {
  padding: 24px;
  border-bottom: 1px solid var(--card-border);
}

.section:last-child {
  border-bottom: none;
}

.order-details {
  padding: 32px;
  max-width: 800px;
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 32px;
}

.order-meta h2 {
  margin: 0;
  font-size: 24px;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.order-date {
  margin: 8px 0 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.section {
  margin-bottom: 32px;
}

.section h3 {
  font-size: 18px;
  margin: 0 0 16px;
}

.items-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.item {
  display: flex;
  gap: 16px;
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
}

.item img {
  width: 60px;
  height: 84px;
  object-fit: contain;
  border-radius: 8px;
}

.item-info {
  flex: 1;
}

.item-info h4 {
  margin: 0;
  font-size: 16px;
}

.item-meta {
  margin-top: 8px;
  color: var(--text-secondary);
  font-size: 14px;
}

.item-total {
  font-weight: 600;
  color: var(--accent-primary);
}

.address-info p {
  margin: 8px 0;
}

.tracking-info {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid var(--card-border);
}

.tracking-link {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  color: var(--accent-primary);
  text-decoration: none;
  margin-top: 8px;
}

.summary-rows {
  background: var(--surface-background);
  border-radius: 12px;
  padding: 16px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  color: var(--text-secondary);
}

.summary-total {
  display: flex;
  justify-content: space-between;
  padding-top: 16px;
  margin-top: 8px;
  border-top: 1px solid var(--card-border);
  font-weight: 600;
  font-size: 18px;
}

.details-footer {
  margin-top: 32px;
  padding-top: 24px;
  border-top: 1px solid var(--card-border);
  display: flex;
  justify-content: flex-end;
}

.pay-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s ease;
}

.pay-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* Match existing status badge styles */
.status-badge {
  padding: 6px 12px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}
</style>
